import React, { useState } from 'react';
import {Link, useHistory } from 'react-router-dom';
import './AddCourseUsers.css';

export default function AddCourseUsers(props){
    let history = useHistory();
    const [maxUsers, setMaxUsers] = useState(props.location.state.quantity);
    const [alert ,setAlert] =useState("");
    const EmailBox = () =>{
                 return(
                        <div className="email_input_box" >                            
                                <input className="user_email_input" type="email" placeholder="Enter user's email..." ></input>                          
                        
                                <input className="already_registered_checkbox" type="checkbox"></input>                                                   
                        </div>)
    }
    const [addComponent, setAddComponent] = useState([]);

    const addUser = () =>{
        console.log(maxUsers , addComponent.length);
        if(addComponent.length === (maxUsers-2) ){
            setAlert ("You have reached the maximum limit..!");
        }else{
            setAddComponent(addComponent => [...addComponent, <EmailBox/> ] );
        }
    }
    return(
        <div className="add_users_container">
            
            <div className="add_users_back_btn" onClick={ () => { history.goBack();} } >  <span className="leftArrow" > &larr; </span> BACK  </div>
            <div className="courseName">Course Name:{props.location.state.courseName}</div>
            <div className="add_users_box">
                <div className="add_emails_container">
                    <div className="add_emails_container_header" >
                        <div className="my-3 add_users_text">Add Users</div>
                        <div className="my-3 already_registered_text">Is Already Registered?</div>
                    </div>

                    <div className="add_emails_container_body">   
                        
                        <div className="email_input_box" >
                        
                                <input className="user_email_input" type="email" placeholder="Enter user's email..." ></input>
                             
                        
                                <input className="already_registered_checkbox" type="checkbox"></input>
                                                     
                        </div>
                        {addComponent}                                         
                    </div>                                       
                </div>
               
            </div>
            <div className="add_email_btn_container">
                <div className="add_email_btn" onClick={addUser} > Add User </div>
            </div>
            <div className="quantity_alert">
                {alert}
            </div>
            <div className="add_users_submit"> 
                <div>
                   <input className="add_users_submit_btn" type="button" value="Submit"></input>
                </div>                
            </div>
        </div>
    )
}