import React,{useEffect, useState} from 'react';
import './BuyNow.css';
import img from './images.jpg';
import logo from './Student/user/logo.png';
import {Switch , Route ,Link} from 'react-router-dom';
import AddCourseUsers from './AddCourseUsers';







export default function BuyNow(){
    const [price,setPrice] = useState(500);
    const [totalPrice,setTotalPrice] = useState(500);
    const [finalPrice,setFinalPrice] = useState(500);
    const [quantity,setQuantity] = useState(1);
    const [haveCoupon, setHaveCoupon] = useState(false);
    const [couponCode,setCouponCode] =useState("");
    const [haveGSTIN, setHaveGSTIN] = useState(false);
    const [GSTDesc,setGSTDesc] =useState("");
    const [validCouponCode ,setValidCouponCode ] = useState("coupon100");
    const [valid , isValid] =useState(false);
    const [discount ,setDiscount] =useState(null);
    const [couponDesc , setCouponDesc] =useState("");
    const [GSTIN ,setGSTIN] =useState("");
    const [validGSTIN , setValidGSTIN] =useState("1234");
    const [GSTINValid, setGSTINValid] = useState(false);
    const [addUsers,setAddUsers] =useState(false);

    const haveCouponClick =()=>{
        setHaveCoupon(true);
    }
    const haveGSTINClick =()=>{
        setHaveGSTIN(true);
    }
    const changeCouponCode = (e) =>{
        setCouponCode(e.target.value);
    }
    const changeGSTIN = (e) =>{
        setGSTIN(e.target.value);
    }
    const incQuantity = () => {
        setQuantity(quantity+1);
        setTotalPrice(price*(quantity +1));
        setFinalPrice(price*(quantity +1));
        if(valid){
            setDiscount( (price*(quantity +1)) * 0.2 );
            setFinalPrice( (price * (quantity +1 )) - ((price*(quantity +1)) * 0.2 ));
        }    
    }
    const decQuantity = () => {
        if(quantity <= 1){
            setQuantity(1);
            setTotalPrice(price*1);
            setFinalPrice(price*1);
            if(valid){
                setDiscount( price * 0.2 );
                setFinalPrice( price - ( price * 0.2 ));
            }else{
                setDiscount(0);
            }    
        }else{
            setQuantity(quantity-1);
            setTotalPrice(price* (quantity -1));
            setFinalPrice(price*(quantity -1));
            if(valid){
                setDiscount( (price*(quantity -1))  * 0.2 );
                setFinalPrice( (price * (quantity - 1 )) - ((price*(quantity -1)) * 0.2 ));
            }else{
                setDiscount(0);
            }      
        }
    }
    const validateCoupon = () =>{
        if(couponCode === validCouponCode){
            setCouponDesc("20% Discount");
            isValid(true);
            setDiscount( totalPrice * 0.2 );
            setFinalPrice(totalPrice - (totalPrice * 0.2) );
        }else{
            setCouponDesc("Invalid Coupon Code !");
            isValid(false);
            setFinalPrice(totalPrice * quantity);
        }
    }
    const validateGSTIN = () =>{
        if(GSTIN === validGSTIN){
            setGSTDesc("GSTIN verified");
            setGSTINValid(true);
        }else{
            setGSTDesc("Invalid GSTIN...!");
            setGSTINValid(false);
        }
    }
    useEffect(()=>{
        if(quantity>1){
            setAddUsers(true);
        }else{
            setAddUsers(false);
        }
    })
     return(
         <div className="buyNow_container ">
             <div className="buyNow_header p-3">
                  <div className="buyNow_header_back_btn"> <span className="leftArrow" > &larr; </span> BACK</div>
                  <div className="buyNow_header_logo"> <img src={logo} ></img> </div>
                  <div className="buyNow_header_link_div">
                      <div className="buyNow_header_links"> <Link  style={{ textDecoration: 'none' }} to="./MyCart" >  Cart  </Link> </div>
                      <div className="buyNow_header_links"> <Link  style={{ textDecoration: 'none' }} to="./WishList" >  Wishlist  </Link> </div>
                      <div className="buyNow_header_links"> <Link  style={{ textDecoration: 'none' }} to="./SaveForLater" >  Save for later  </Link> </div>
                  </div>
             </div>

             <div className="buyNow_modal mt-5">

                <div className="modal_left">                     
                    <div  className="coursesFrom">
                        <div> From </div>
                        <ul>
                            <li>Name    : John</li>
                            <li>Contact : +6787268877</li>
                            <li>E-mail  : example@gmail.com</li>
                        </ul>
                    </div>                     
                </div>

                <div class=" buyNow_card  ">

                    <div class=" buyNow_card_header py-3">
                        <div className="buyNow_card_header_left">Item Name</div>
                        <div className="buyNow_card_header_right">
                            <div>Item Price</div>
                            <div>Action</div>
                        </div>
                    </div>
                    <div class=" buyNow_card_body py-4">
                        <div className="buyNow_card_body_left">
                            <div> <img className="buyNow_card_body_img " src={img}></img> </div>
                            <div className="buyNow_card_body_desc">
                                <div className="buyNow_card_body_name">Heroic Coaching</div>
                                <div className="buyNow_card_body_quantity">
                                    <span>Quantity</span>
                                    <span className="buyNow_card_body_quantity_dec" onClick={decQuantity}> - </span>
                                    <span> {quantity} </span>
                                    <span className="buyNow_card_body_quantity_inc" onClick={incQuantity}> + </span>
                                </div>
                            </div>
                        </div>
                        <div className="buyNow_card_body_right">
                            <div className="buyNow_card_body_price">₹ {price}</div>
                            <div className="buyNow_card_body_action">
                                <div>Add to WishList</div>
                                <div>Save for Later</div>
                                <div>Remove</div>
                            </div>
                        </div>
                    </div>
                    <div class=" buyNow_card_footer pb-2">
                        <div className="buyNow_card_footer_left" >
                            <div className={haveCoupon ?  "buyNow_card_footer_left_flex" : "hide" }>
                                <div>
                                    <input className="buyNow_card_footer_c_input" onChange={changeCouponCode} type="text" placeholder="Enter coupon code..."></input>
                                </div>
                                <div>
                                    <input className="buyNow_card_footer_c_btn" onClick={validateCoupon} type="button" value="Apply Coupon Code"  ></input>                            
                                </div> 
                            </div>
                            <div>
                                <div className={valid ? "couponDesc" : "couponDesc red"} > 
                                    { couponDesc} 
                                </div>
                            </div>

                            {/* GSTIN input field */}

                            <div className={haveGSTIN ? "buyNow_card_footer_left_flex" : "hide" }>
                                <div>
                                    <input className="buyNow_card_footer_gst_input" onChange={changeGSTIN} type="text" placeholder="Enter GSTIN..."></input>
                                </div>
                                <div>
                                    <input className="buyNow_card_footer_gst_btn" onClick={validateGSTIN} type="button" value="Verify GSTIN"  ></input>                            
                                </div> 
                            </div>
                            <div>
                                <div className={GSTINValid ? "gstinDesc " : "gstinDesc red"} > 
                                    { GSTDesc} 
                                </div>
                            </div>
                        </div>    
                        <div className="total_price">
                            <div className="total_price_label">Total</div>
                            <div className= { valid ? "discountAmount" : "dispNone" }>
                                <div> ₹ {totalPrice} </div>
                                <div className="border_bottom pb-2">- ₹{discount}</div>                                
                            </div>
                            <div className="total_price_amt"> ₹{ finalPrice}</div>
                        </div>
                    </div>

                    <div>
                        {addUsers ?                        
                            <div className="payNow">
                                <Link  style={{ textDecoration: 'none' }} to ={{pathname: "/AddCourseUsers", state: { courseName: " Heroic Coaching" , quantity : quantity } }} > <input className="payNow_btn" type="button" value="Next"></input> </Link>
                            </div>
                        :
                            <div className="next">
                                <Link  style={{ textDecoration: 'none' }} to="./AddCourseUsers" > <input className="next_btn" type="button" value="Pay Now"></input> </Link>
                            </div>
                        }
                    </div>
                </div>

                <div className="modal_right">
                    <div className="coursesTo">
                        <div> To </div>
                        <ul>
                            <li>Name    : Henry</li>
                            <li>Contact : +6787268877</li>
                            <li>E-mail  : example@gmail.com</li>
                        </ul>
                    </div>
                </div>

             </div>

             <div className="combo_Button_container my-4">
                 <div className="combo_button">
                    <div className="couponCode_button p-3" onClick={haveCouponClick} >
                        Have a coupon code?
                    </div>
                    <div className="GSTIN_button p-3" onClick={haveGSTINClick}>
                        Have a GSTIN?
                    </div>
                 </div>
             </div>
        
         </div>       
     );
}